import axios from 'axios'
export default {
  name: 'checkProjectTaskForm',
  data () {
    return {
      projectID: '',
      task: '',
      okTask: false,
      name: '',
      startTime: '',
      endTime: '',
      content: '',
      target: '',
      leaderName: ''
    }
  },
  methods: {
    getTask () {
      axios.get(this.url + '/module/id/' + sessionStorage.getItem('taskID'))
        .then(this.getTaskSucc)
    },
    getTaskSucc (res) {
      if (res.status === 200) {
        res = res.data
        this.task = res
        this.name = res.name
        this.startTime = res.startTime
        this.endTime = res.endTime
        this.content = res.content
        this.target = res.target
        this.leaderName = res.leaderName
        this.projectID = res.projectID
      }
    },
    submit () {
      this.$axios({
        method: 'put',
        url: this.url + '/module/',
        data: {
          id: parseInt(sessionStorage.getItem('taskID')),
          leaderID: this.task.leaderID,
          name: this.name,
          startTime: this.startTime,
          endTime: this.endTime,
          content: this.content,
          target: this.target
        }
      }).then((res) => {
        if (res.status === 200) alert('提交成功！')
        else alert('提交失败！请重新打开页面')
        this.okTask = false
        this.$router.push('/checkProjectDetail')
      }).catch((err) => {
        alert(err)
      })
    }
  },
  mounted () {
    this.getTask()
  }
}



